.recommended {
  width: 100%;
  display: block;
  max-width: 1200px;
  margin: 0 auto;
  padding: 30px 50px;

  * {
    box-sizing: border-box;
    font-family: Arial, "Helvetica Neue", sans-serif;
  }

  .dots {
    padding-bottom: 30px;

    button {
      background: #000 !important;
    }
  }

  h2 {
    text-align: center;
  }

  time {
    font-size: 12px;
    font-weight: bold;
    text-align: right;
    display: block;
  }

  .poster-item {
    width: 100%;
    position: relative;

    img {
      width: 100%;
      display: block;
    }

    .rate {
      width: 100%;
      height: 0;
      overflow: hidden;
      opacity: 0;
      visibility: hidden;
      position: absolute;
      bottom: 0;
      left: 0;
      background-color: rgba(183, 183, 183, 0.6);
      display: flex;
      -webkit-transition: height 0.2s;
      -moz-transition: height 0.2s;
      -ms-transition: height 0.2s;
      -o-transition: height 0.2s;
      transition: height 0.2s;

      ul {
        margin: 0 auto;
        padding: 9px 0;
      }
    }

    &:hover {
      .rate {
        height: 35px;
        opacity: 1;
        visibility: visible;
      }
    }
  }

}
